.search-results{margin-top:20px}.box-loading{border:none}.search-results-date{font-weight:bold;margin-bottom:10px}.search-result{margin-bottom:5px}.search-result-time{display:block;font-size:11px;height:24px;line-height:24px;margin-right:5px;text-align:right}.search-result-price{border-color:transparent !important;color:white !important;float:left}.price-low{background-color:#27ae60}.price-low:hover{background-color:#295}.price-medium{background-color:#e6a83c}.price-medium:hover{background-color:#e39e25}.price-high{background-color:#e74c3c}.price-high:hover{background-color:#e43725}@media only screen and (max-width: 979px){.box{border-radius:0}.search-result{display:inline-block;margin-right:10px}.search-result-time{text-align:center}.search-result-price{float:none}}@media only screen and (max-width: 768px){.search-result-time{margin-right:0}}



